import { createApp } from 'vue'
import App from './App.vue';
import "./styles/reset.less";
import "./styles/myStyle.less";
import router from "@/routers";
import store from "@/store";
import linkPlugin from "@/plugins/link.js";
import {
    // create naive ui
    create,
    // component
    NButton,
    NCarousel,
    NDropdown,
    NIcon,
    NAvatar,
    NPagination
} from 'naive-ui'
store.dispatch("User/whoami");

const naive = create({ // 按需全局引入组件
    components: [NButton, NCarousel,NDropdown,NIcon,NAvatar,NPagination]
})

createApp(App).use(router).use(store).use(naive).use(linkPlugin).mount('#app')

